import {TouchableOpacity, View} from 'react-native';
import TextCM from '../../components/Text';
import styles from './styles';
import {useAppDispatch, useAppSelector} from '../../../hooks/useRedux';
import {useTranslation} from 'react-i18next';
import {IconCheckmarkCircle} from '../../../assets/images';
import {useAccountSettingService} from '../../../hooks/apiHooks/useAccountInfoServices';
import Toast from 'react-native-toast-message';
import {accountActions, commonActions} from '../../../redux/slices';

const TranferOrganization = () => {
  const {dataAccount} = useAppSelector(state => state.account);
  const {t} = useTranslation();
  const AccountSettingServices = useAccountSettingService();
  const dispatch = useAppDispatch();

  const handleTransferOrganization = async (orgIn: string) => {
    if (orgIn === dataAccount.orgIn) return;
    try {
      let data = await AccountSettingServices.subOrg(
        dataAccount.email || '',
        orgIn,
        '',
      );
      Toast.show({
        type: 'success',
        text1: t('ST.TxtChangeOrgSuccess'),
      });
      dispatch(commonActions.setAccessToken(data.access_token));
      dispatch(accountActions.setInfoResponseLogin(data));
      dispatch(accountActions.fetchAccount());
    } catch (error) {}
  };
  return (
    <View style={styles.menuContainer}>
      <TextCM style={styles.sectionTitle}>{t('ST.TxtOrganization')}</TextCM>
      <TouchableOpacity
        style={[
          styles.menuItem,
          dataAccount.organization.orgIn === dataAccount.orgIn && {
            borderLeftWidth: 8,
            borderLeftColor: '#943cdd',
            borderRightWidth: 8,
            borderRightColor: '#943cdd',
          },
        ]}
        onPress={() =>
          handleTransferOrganization(dataAccount.organization.orgIn)
        }>
        <TextCM
          style={[
            {
              color:
                dataAccount.organization.orgIn === dataAccount.orgIn
                  ? '#943cdd'
                  : 'black',
            },
            styles.txtOrg,
          ]}>
          {dataAccount.organization.name}
        </TextCM>
        {dataAccount.organization.orgIn === dataAccount.orgIn ? (
          <IconCheckmarkCircle
            width={24}
            height={24}
            fill={'#943cdd'}
            style={{paddingHorizontal: 5}}
          />
        ) : null}
      </TouchableOpacity>
      <TextCM style={styles.sectionTitle}>{t('ST.TxtSubOrganization')}</TextCM>
      {dataAccount.subOrganizations.length > 0 ? (
        dataAccount.subOrganizations.map((subOrg: any) => (
          <TouchableOpacity
            key={subOrg.orgIn}
            style={[
              styles.menuItem,
              subOrg.orgIn === dataAccount.orgIn && {
                borderLeftWidth: 8,
                borderLeftColor: '#943cdd',
                borderRightWidth: 8,
                borderRightColor: '#943cdd',
              },
            ]}
            onPress={() => handleTransferOrganization(subOrg.orgIn)}>
            <TextCM
              style={{
                color: subOrg.orgIn === dataAccount.orgIn ? '#943cdd' : 'black',
              }}>
              {subOrg.name}
            </TextCM>
            {subOrg.orgIn === dataAccount.orgIn ? (
              <IconCheckmarkCircle
                width={24}
                height={24}
                fill={'#943cdd'}
                style={{paddingHorizontal: 5}}
              />
            ) : null}
          </TouchableOpacity>
        ))
      ) : (
        <TextCM>{t('ST.TxtNoSubOrg')}</TextCM>
      )}
    </View>
  );
};

export default TranferOrganization;
